@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Varela&display=swap);
@import url(https://fonts.googleapis.com/css2?family=BioRhyme:wght@800&family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@200;300;400;600;700;900&display=swap);
@import url(https://fonts.googlyarn starteapis.com/css2?family=JetBrains+Mono:wght@100;200;300;400;500;600;700;800&display=swap);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 10px;
  font-family: 'Montserrat', sans-serif;
}
body{
  background-color: rgb(243, 243, 243);
  animation: come-in 700ms ease-in;
}
@keyframes come-in {
  0%{
    opacity: 0;
  }
  50%{
    opacity: 0.5;
  }
  100%{
    opacity: 1;
  }
}
.upnav{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    background-color: rgb(217, 233, 233);
}
.upnav_in{
    height: 50px;
    width: 90%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
}
.upnav_item ul{
    width: 100px;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}
#left_upnav{
    width: 300px;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}
#left_upnav span{
    transform: scale(1.3);
}
#right_upnav li{
    transform: scale(2);
}
@media (max-width: 768px) and (min-width:481px)
{
    .upnav_item ul{
        width: 80px;
    }
    #left_upnav{
        width: 250px;
    }
    #left_upnav span{
        transform: scale(1.2);
    }
}
@media screen and (max-width:480px)
{
    .upnav_item ul{
        width: 60px;
    }
    #mail{
        display: none;
    }
    #left_upnav span{
        transform: scale(1.2);
    }
    #left_upnav{
        width: 150px;
    }
    #right_upnav li{
        transform: scale(1.5);
    }
}
nav{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    height: 80px;
    border-bottom: 2px solid rgb(245, 240, 240);
    z-index: 3;
    background-color: white;
}
.nav{
    width: 90%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
}
.heading{
    position: relative;
    font-weight: 900;
    width: 250px;
    text-shadow: 1px 1px 2px rgb(204, 198, 198);
}
.heading .pencil{
    position: absolute;
    top:50%;
    left: 5%;
    padding-right: 3px;
    color: rgb(20, 83, 165);
    transform: scale(4);

}
.heading .text{
    position: relative;
    left:20%;
    padding-right: 3px;
    color: black;
    font-size: 3.4rem;
    font-family: 'BioRhyme', serif;
}
#menu > ul{
    width: 500px;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
}
.link{
    position: relative;
    font-size: 1.5rem;
    padding: 10px 20px;
    font-weight: 600;
    border-radius: 30px;
    transition: all;
    transition-duration: 0.2s;
}
.link:hover{

    background-color:rgb(55, 29, 170);
    color:white;
}

.link:active{

    background-color:rgb(65, 3, 235);
    color:white;
}
.link_active{
    border-radius: 30px;
    color: white;
    background-color:rgb(55, 29, 170);
}
#menu > ul li{
    
    position: relative;
    
}
#menu > ul li ul{
    position: relative;
    z-index: 2;
    display: none;
    width: 120%;
    position: absolute;
    text-align: center;
    left: calc(50% - 60%);
    margin-top: 11px;
    background-color: rgb(233, 241, 240);
    border-radius: 10px;
    box-shadow: 2px 2px 2px 1px rgb(226, 226, 226);
}
#courses:hover ~ ul{
    transform-origin: top center;
    display: block;
    animation: growDown 300ms ease-in-out;
}
@keyframes growDown {
    0% {
        transform: scaleY(0)
    }
    80% {
        transform: scaleY(1.1)
    }
    100% {
        transform: scaleY(1)
    }
}
#menu > ul li ul:hover{
    display: block;
}
.inner_link{
    display: block;
    text-decoration: none;
    font-size: 1.4rem;
    color: black;
    padding: 12px 0px;
}
.inner_link:hover {
    background-color: rgb(228, 228, 228);
}
#courses{
    left:0;
}
.inner_ul:nth-child(1){
    border-bottom: 2px solid rgb(165, 158, 158);
}

.hamburger_menu{
    display: none;
}
.hamburger_menu a{
    color: rgb(23, 23, 66);
    transform: scale(1);
}
.bar{
    border-radius: 5px;
    width: 27px;
    height: 27px;
}
.sidebar{
    position: fixed;
    right: -100%;
    top: 0;
    background-color: rgb(23, 23, 66);
    height: 100vh;
    width: 45vw;
    transition: 350ms;
    overflow: auto;
}
.sidebar.active{
    right: 0;
}
.cross_container{
    position: fixed;
    left: -100%;
    top: 2%;
    transition: 350ms;
}
.cross_container.active{
    left: 2%;
}
.cross_container a{
    color: white;
}
.cross{
    width: 50px;
    height: 50px;
    border-radius: 2px;
    background-color: rgb(23, 23, 66);
}
.list_container{
    margin: 20px;
}
.list_container ul li{
    position: relative;
    width: 100%;
    margin: 20px 5px;
}

.list_container ul li a{
    width: 100%;
    color: white;
    text-decoration: none;
    font-size: 1.7rem;
    font-weight: 500;
}
.sidebar_link_active{
    padding: 5px;
    border-radius: 10px;
    background-color:rgb(63, 30, 197);
}
#inner_list li a{
    position: relative;
    font-weight: 400;
}
#inner_list{
    transform-origin: top center;
    animation: down 100ms;
}
@keyframes down {
    0%{
        transform: scale(0);
    }
    50%{
        transform: scale(0.5);
    }
    100%{
        transform: scale(1);
    }
    
}
.down{
    position: absolute;
    right: 0;
    height: 20px;
    width: 20px;
}
@media (max-width: 950px) and (min-width: 769px) {
    #menu > ul{
        width: 380px;
    }
    .link{
        padding: 5px 10px;
    }
}
@media (max-width: 768px) and (min-width:481px)
{
    #menu > ul{
        display: none;
    }
    .heading .pencil{
        transform: scale(3);
        top: 48%;
        left: 3%;
    }
    .heading .text{
        left:15%;
        font-size: 3rem;
    }
    .hamburger_menu{
        display: block;
    }
}
@media screen and (max-width:480px)
{
    #menu > ul{
        display: none;
    }
    .heading .pencil{
        transform: scale(2.6);
        left: 3%;
        top: 44%;
    }
    .heading .text{
        left:13%;
        font-size: 2.1rem;
    }
    nav{
        height: 50px;
    }
    .hamburger_menu{
        display: block;
    }
}
.banner_content{
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(to right, #30b974,#17d5db), url(/ssacademy/static/media/banner.c281750d.jpg);
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}
.welcome{
    letter-spacing: 2px;
    text-align: center;
    color: white;
    font-size: 6rem;
    font-family: 'Cairo', sans-serif;
    text-shadow: 3px 4px 3px rgb(12, 12, 12); 
}
.tagline{
    font-weight: 600;
    letter-spacing: 3px;
    color: rgb(6, 247, 214);
    font-size: 2rem;
    text-shadow: 2px 2px 2px rgb(12, 12, 12);
}
.button{
    position: relative;
    background-color: rgb(211, 31, 31);
    color: white;
    font-size: 2rem;
    margin-top: 50px;
    padding: 10px 20px;
    border-radius: 30px;
    font-weight: 500;
    text-shadow: 1px 1px 1px rgb(12, 12, 12);
    box-shadow: 1px 1px 2px 1px rgb(148, 10, 10);
    transition-duration: 0.2s;
    z-index: 3;
}
.button::before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0%;
    height: 0%;
    width: 100%;
    background: rgb(55, 29, 170);
    z-index: -1;
    border-radius: 30px;
    transition-duration: 0.2s;
}
.button:hover{
    background: none;
    box-shadow: 1px 1px 2px 1px rgb(39, 19, 128);
}
.button:hover::before{
    height: 100%;
}
.button:active{
    background-color: rgb(32, 16, 100);
}

.wave{
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 2;
}
@media (max-width: 950px) and (min-width: 769px){
    .welcome{
        font-size: 5rem;
    }
}
@media (max-width: 768px) and (min-width:481px){
    .welcome{
        font-size: 4.3rem;
        line-height: 1.5;
    }
}
@media screen and (max-width:480px){
    .welcome{
        font-size: 4rem;
        line-height: 1.3;
    }
    .tagline{
        margin-top: 20px;
    }
}
.welcome_ss{
    width: 100%;
    margin: 30px auto;
    height: auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
}
.wel_item{
    width: 40%;
    height: 390px;
    margin: auto 4%;
}
.welcome_img{
    border-radius: 50%;
    background: url(/ssacademy/static/media/welcomeMessage.724a279f.jpg) no-repeat center center/cover;
    width: 390px;
    box-shadow: 8px 8px 8px 1px rgb(160, 160, 160);
}
.welcome_left h3, .owner_left h3{
    color: #212127;
    margin: 0 0 30px 0;
    position: relative;
    font-size: 2.89rem;
    font-weight: 600;
}
.owner_left h3{
    color: black;
}
.welcome_left h3::after, .owner_left h3::after{
    content: '';
    position: absolute;
    top: 120%;
    left: 0;
    width: 320px;
    height: 2px;
    background: rgb(4, 6, 148);
}
.owner_left h3::after{
    width: 280px;
    background: rgb(233, 0, 0);
}
.welcome_left p, .owner_left p{
    margin: 10px auto;
    font-size: 1.5rem;
    text-align: justify;
    color: #6b6b6b;
}
.owner_s{
    margin: 120px auto;
    display: grid;
    grid-template-columns: 0.7fr 8fr 0.7fr;
}
.owner_ss{
    position: relative;
    display: grid;
    width: 100%;
    background-color: #d4d4d4;
    grid-column: 2/3;
    grid-template-columns: 1.5fr 1fr;
    grid-column-gap: 10%;
    -webkit-column-gap: 10%;
            column-gap: 10%;
    padding: 30px 51px;
    border-radius: 40px;
    box-shadow: 2px 2px 7px 4px #B4B4B4;
}
.owner_img{
    height: 320px;
}
.owner_left{
    min-height: 320px;
}
.owner_left p{
    font-size: 1.4rem;
    color: #5a5a5a;
    font-style: italic;
    text-align: justify;
}
.owner_img{
    background: url(/ssacademy/static/media/ownerMessage.e093b038.jpeg) no-repeat top center/cover;
    width: 100%;
    border-radius: 10px;
    box-shadow: 1px 1px 4px 1px #B4B4B4;
}
@media (max-width: 950px) and (min-width: 769px) {
    .wel_item{
        margin: auto 2%;
    }
    .welcome_ss{
        justify-content: space-between;
    }
    .owner_s{
        grid-template-columns: 0.1fr 5fr 0.1fr;
    }
}
@media (max-width: 768px) and (min-width:481px)
{
    .wel_item{
        width: 80%;
        margin: auto 4%;
    }
    .welcome_img{
        width: 400px;
        margin-bottom: 40px;
    }
    .welcome_left{
        height: auto;
    }
    .welcome_left h3, .owner_left h3{
        text-align: center;
    }
    .welcome_left h3::after{
        left: 50%;
        margin-left: -160px;
    }
    .owner_left h3::after{
        left: 50%;
        margin-left: -137px;
    }
    .welcome_ss{
        flex-flow:row wrap-reverse;
    }
    .owner_s{
        margin-top: 10px;
        grid-template-columns: 0.1fr 1fr 0.1fr;
    }
    .owner_ss{
        grid-template-columns: 1fr;
        grid-template-rows: 320px 1fr;
        grid-row-gap: 5%;
        row-gap: 5%;
    }
    .owner_img{
        grid-row: 1/2;
    }
    .owner_left{
        margin-bottom: 30px;
    }
}
@media screen and (max-width:480px)
{
    .wel_item{
        width: 80%;
        margin: auto 4%;
    }
    .welcome_img{
        width: 300px;
        height: 300px;
        margin-bottom: 40px;
    }
    .welcome_left{
        height: auto;
    }
    .welcome_left h3, .owner_left h3{
        font-size: 2.2rem;
        text-align: center;
    }
    .welcome_left h3::after{
        width: 270px;
        left: 50%;
        margin-left: -135px;
    }
    .welcome_ss{
        flex-flow:row wrap-reverse;
    }
    .owner_left h3::after{
        width: 230px;
        left: 50%;
        margin-left: -115px;
    }
    .owner_s{
        margin-top: 10px;
        grid-template-columns: 0.1fr 1fr 0.1fr;
    }
    .owner_ss{
        grid-template-columns: 1fr;
        grid-template-rows: 250px 1fr;
        grid-row-gap: 3%;
        row-gap: 3%;
        padding: 20px 20px;
    }
    .owner_img{
        height: 250px;
        grid-row: 1/2;
    }
    .owner_left{
        margin-bottom: 30px;
        min-height: 320px;
    }
}
.r_heading{
    display: block;
    text-align: center;
    width: 800px;
    font-size: 4rem;
    border-bottom: 3px solid rgb(233, 124, 0);
    margin: 10px auto;
    padding: 5px;
    color: rgb(25, 6, 61);
}
.r_cards{
    display: grid;
    margin: 50px auto;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px 20px;
}
.r_card{
    display: flex;
    flex-flow: row wrap;
    width: 100%;
    height: 250px;
}
.r_card_img{
    border-radius: 20px 0px 0px 20px;
}
.r_card_i{
    width: 50%;
}
.r_card_detail{position: relative;
    padding: 10px;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: flex-start;
    background-color: white;
    border-radius: 0px 20px 20px 0px;
}
.marks{
    line-height: 50px;
    text-transform: capitalize;
}
.marks p{
    color: rgb(0, 16, 104);
    font-size: 1.6rem;
    font-weight: 700;
}
.marks span{
    font-size: 1.9rem;
    font-weight: 600;
    color: rgb(189, 16, 16);
}
.s_info{
    position: absolute;
    bottom: 5%;
}
.s_info p{
    font-size: 1.3rem;
    font-weight: 500;
    color: rgb(77, 77, 77);
}
.s_info p:nth-child(1){
    font-weight: 700;
    font-size: 1.5rem;
}
@media screen and (min-width:1700px){

    .r_card{
        height: 400px;
    }
    .marks p{
        font-size: 2.4rem;
    }
    .marks span{
        font-size: 2.7rem;
    }
    .s_info p{
        font-size: 1.7rem;
    }
    .s_info p:nth-child(1){
        font-size: 2rem;
    }
}
@media (max-width:1200px) and (min-width:951px){
    .r_card{
        height: 200px;
    }
    .marks{
        line-height: 25px;
    }
    .marks p{
        font-size: 1.2rem;
    }
    .marks span{
        font-size: 1.5rem;
    }
    .s_info p{
        font-size: 1rem;
    }
    .s_info p:nth-child(1){
        font-size: 1.2rem;
    }
}
@media (max-width:950px) and (min-width:769px){
    .r_heading{
        font-size: 3rem;
        width: 600px;
    }
    .r_cards{
        grid-template-columns: 1fr 1fr;
    }
    .marks{
        line-height: 35px;
    }
    .marks p{
        font-size: 1.5rem;
    }
    .marks span{
        font-size: 1.7rem;
    }
    .s_info p{
        font-size: 1.2rem;
    }
    .s_info p:nth-child(1){
        font-size: 1.3rem;
    }
}
@media (max-width: 768px) and (min-width:481px){
    .r_heading{
        font-size: 2.3rem;
        width: 470px;
    }
    .r_cards{
        grid-template-columns: 1fr;
        margin: 30px auto;
    }
    .r_card{
        height: 350px;
    }
    .marks{
        line-height: 60px;
    }
    .marks p{
        font-size: 2rem;
    }
    .marks span{
        font-size: 2.3rem;
    }
    .s_info p{
        font-size: 1.6rem;
    }
    .s_info p:nth-child(1){
        font-size: 1.8rem;
    }
}
@media screen and (max-width:480px){
    .r_heading{
        background-color: rgb(248, 245, 245);
        font-size: 2.2rem;
        width: 95%;
        margin: 0px auto;
        padding: 5px 0px;
        border: 3px solid rgb(233, 124, 0);
        border-radius: 20px;
        box-shadow: 1px 1px 2px 5px rgb(226, 225, 221);
    }
    .r_cards{
        grid-template-columns: 1fr;
        margin: 35px auto;
    }
    .r_card{
        height: 250px;
    }
    .marks{
        line-height: 30px;
    }
    .marks p{
        font-size: 1.45rem;
    }
    .marks span{
        font-size: 1.65rem;
    }
    .s_info p{
        font-size: 1.6rem;
    }
    .s_info p:nth-child(1){
        font-size: 1.8rem;
    }
    .r_card_detail{
        padding: 5px;
    }
}
footer{
    display: flex;
    flex-flow: row wrap;
    background-color: #212127;
    color: white;
    box-sizing: border-box;
    justify-content: center;
}
footer div h6{
    font-size: 2.1rem;
    font-weight: 500;
}
.footer_i{
    width: 270px;
    margin: 30px;
}
footer p{
    line-height: 20px;
    color: #dddd;
    margin: 20px auto;
    font-size: 1.3rem;
}
footer p span{
    color: white;
    font-size: 1.3rem;
}
.address span{
    position: relative;
